import React from 'react';
import { useProductState } from '../products-state';
import { Link } from 'gatsby';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

let Breadcrumb = (props) => {
  let { getMarketByPath } = useProductState();

  let market = getMarketByPath();

  return (
    <Breadcrumbs aria-label="breadcrumb">
      <span><Link to="/productos">Productos</Link></span>
      <span><Link to={`/productos/${market.slug}`}>{market.name}</Link></span>
    </Breadcrumbs>
  );
};

export default Breadcrumb;

