import React from 'react';
import Cookies from 'js-cookie';
import Modal from 'react-modal';
import VideoGallery from '../../../../../components/video-gallery'
import { useLibraryState } from '../library-state';
import GridCatalogs from '../grid-catalog';
import TableCatalog from './catalog-table';
import ModalCatalog from './modal-catalog';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Grid from '@material-ui/core/Grid';
import get from 'lodash/get';

Modal.setAppElement('#___gatsby');
const modalStyles = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)"
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "120px auto",
    padding: 0,
    border: 0
  }
};

const modalStylesMobile = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)"
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "20px auto",
    padding: 0,
    border: 0
  }
};
let Main = ({ path, context }) => {
  let { getData } = useLibraryState();
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let libraryData = getData();
  let [showContactYou, setShowContactYou] = React.useState(false);

  let [toggleModal, setToggleModal] = React.useState(false);
  let refToModal = React.useRef(null);

  let closeModal = () => {
    setToggleModal(false);
    setShowContactYou(false);
    refToModal.current = null;
  }

  let handleSelectCatalog = (data) => {
    refToModal.current = {
      data,
      flow: libraryData.flow
    };
    let isCookie = Cookies.get('isRegisteredThreebond') === 'true';
    if (isCookie) {
      if (libraryData.flow === 'tds') {
        let tdsSrc = get(data, 'tdsProduct.publicURL', false);
        if (!tdsSrc) {
          setShowContactYou(true);
        } else {
          window.open(tdsSrc, '_blank');
        }
        return ;
      } else if (libraryData.flow === 'sds') {
        let sdsSrc = get(data, 'sdsProduct.publicURL', false);
        if (!sdsSrc) {
          setShowContactYou(true);
        } else {
          window.open(sdsSrc, '_blank');
        }
        return ;
      } else if (libraryData.flow === 'catalogos') {
        let catalogSrc = get(data, 'file', false);
        if (!catalogSrc) {
          setShowContactYou(true);
        } else {
          window.open(catalogSrc, '_blank');
        }
        return ;
      }
    }

    setToggleModal(true);
  };

  return(
    <section className="product-view__main-section">
      <h1>{libraryData.title}</h1>
      <p>{libraryData.description}</p>
      { libraryData.flow === 'catalogos' && <GridCatalogs catalogs={libraryData.items} onSelectCatalog={handleSelectCatalog} /> }
      { libraryData.flow === 'videos' && <VideosView videos={libraryData.items}/> }
      { libraryData.flow === 'tds' && <TableCatalog families={libraryData.items} onSelectTDS={handleSelectCatalog} /> }
      { libraryData.flow === 'sds' && <TableCatalog families={libraryData.items} onSelectTDS={handleSelectCatalog} /> }
      <Modal
        isOpen={toggleModal || showContactYou}
        style={matchMobile ? modalStylesMobile : modalStyles}
        contentLabel="MODAL"
        className="catalog-modal-container"
      >
        <ModalCatalog apiHostName={context.apiHostName} data={refToModal.current} onClose={closeModal} showContact={showContactYou}/>
      </Modal>

    </section>
  );
};

let VideosView = ({videos}) => {
  return (
  videos.map(video => (
    <Grid container>
      <Grid item xs={12}>
        <Grid container>
          <Grid item md={5}>
            <VideoGallery gallery={video} />
          </Grid>
          <Grid item md={7}>
            <div className="video-description">
            {video.description}
            </div>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  ))
  )
};

export default Main;
