import React from 'react';
import { Link } from 'gatsby';
import { useProductState } from '../products-state';
import Breadcrumb from '../breadcrumb';
import TogglePanel from '../toggle-panel';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames';

let SideBar = (props) => {
  let { getMarkets, getFamilies, getSubtypes,   } = useProductState();
  let matchMobile = useMediaQuery('(max-width: 599px)');

  let slugPath = props.path.split('/')[2];
  let slugPathF = props.path.split('/')[3];

  let markets = getMarkets();
  let subtypes = getSubtypes();
  let families = getFamilies();

  let isMarket = !!markets.find(market => market.slug === slugPath);
  let isSubtype = !!subtypes.find(subtype => subtype.slug === slugPath);
  let isFamilie = !!slugPathF && !!families.find(famly => famly.slug === slugPathF);
  
  return (
    <section className="product__side-bar">
      <Breadcrumb path={props.path}/>
      <TogglePanel title='Mercados' collapsed={!isMarket}>
        {markets.map(market => <Link className={classnames('side-bar-item', { active: slugPath === market.slug})} to={`/productos/${market.slug}`}>{market.name}</Link>)}
      </TogglePanel>
      <TogglePanel title='Productos' collapsed={!isSubtype}>
        {subtypes.map(subtype => <Link className={classnames('side-bar-item', { active: slugPath === subtype.slug})} to={`/productos/${subtype.slug}`}>{subtype.name}</Link>)}
      </TogglePanel>
      <TogglePanel title='Series' collapsed={!isFamilie}>
        {families.map(family => <Link className={classnames('side-bar-item', { active: slugPathF === family.slug})} to={`/productos/serie/${family.slug}`}>{family.name}</Link>)}
      </TogglePanel>
    </section>
  );
}

export default SideBar;

