import React from "react"
import { Link } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames'
import isEmpty from './is-empty';

export default function ModalProduct({ product, onClose, onDownloadTDS }) {
  let matchMobile = useMediaQuery('(max-width: 599px)')
  return (
    <div className={classnames("product-modal", { mobile: matchMobile })}>
      <div className="close-modal-container">
        <button className="close-modal" onClick={onClose}>
          <FontAwesomeIcon
            icon={["fas", "times"]}
            style={{ color: "#000000" }}
          />
        </button>
      </div>
      <div className="modal-body">
        <div className="image-container">
          { matchMobile && <h1>{product.name}</h1> }
          <img
            src={product.imageProduct.childImageSharp.fluid.src}
            alt="product-image"
          />
          {
            !matchMobile && (
              <div className="actions-container">
                <span className="family">
                  <Link to={`/productos/serie/${product.family.name}`}>
                    Ver Serie
                  </Link>
                </span>
                <span className="documents">
                  <button className="btn-ghost" onClick={onDownloadTDS}>
                    <FontAwesomeIcon
                      icon={["fas", "file-download"]}
                      style={{ color: "#000000" }}
                    />
                    &nbsp;TDS/SDS
                  </button>
                </span>
              </div>
            )
          }
        </div>
        <div className="product-specifications">
          { !matchMobile && <h1>{product.name}</h1> }
          <h4>Descripción</h4>
          <p>{product.usages}</p>
          <h4>Características</h4>
          {!isEmpty(product.resistencia) && <p>Resistencia: {product.resistencia}</p>}
          {!isEmpty(product.propiedades) && <p>Propiedades: {product.propiedades}</p>}
          {!isEmpty(product.viscosidad) && <p>Viscosidad(mPa-s): {product.viscosidad}</p>}
          {!isEmpty(product.torsion) && <p>Torsion de ruptura(N/m): {product.torsion}</p>}
          {!isEmpty(product.temperatura) && (
            <p>Rango de temperatura de operación: {product.temperatura}</p>
          )}
          {!isEmpty(product.apariencia) && <p>Apariencia: {product.apariencia}</p>}
          {!isEmpty(product.dureza) && <p>Dureza: {product.dureza}</p>}
          {!isEmpty(product.condicionCurado) && <p>Condicion de curado: {product.condicionCurado}</p>}
          {!isEmpty(product.elongacion) && <p>Elongacion: {product.elongacion}</p>}
          {!isEmpty(product.packaging) && <p>Packaging: {product.packaging}</p>}
          {!isEmpty(product.resistenciaCorte) && <p>Resistencia al corte: {product.resistenciaCorte}</p>}
          {!isEmpty(product.saltSpray) && <p>Salt Spray Test: {product.saltSpray}</p>}
          <br />
          {
            matchMobile && (
              <div className="actions-container">
                <span className="family">
                  <Link to={`/productos/serie/${product.family.name}`}>
                    Ver Serie
                  </Link>
                </span>
                <span className="documents">
                  <button className="btn-ghost" onClick={onDownloadTDS}>
                    <FontAwesomeIcon
                      icon={["fas", "file-download"]}
                      style={{ color: "#000000" }}
                    />
                    &nbsp;TDS/SDS
                  </button>
                </span>
              </div>
            )
          }
          <Link className="btn btn-secondary" to="/cotiza">
            COTIZAR
          </Link>
        </div>
      </div>
    </div>
  )
}
