import React from 'react';

import pages from './views';
import getterProps from './getter-props';

export default function pageFactory({ name, ...context }, props) {
  let Component = pages[name];
  let is404 = false;
  let getProps = getterProps[name] || getterProps['default'];
  if (!Component) {
    Component = pages['404'];
    is404 = true;
  }
  return [(<Component {...getProps(context, props)} />), is404];
};
