import React from 'react'
import SideBar from './components/side-bar';
import LibraryProvider from './components/library-state';
import Main from './components/main-section';
import Grid from '@material-ui/core/Grid';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames';
import { useStaticQuery, graphql } from 'gatsby';

function Biblioteca(props) {
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let {backgroundImg} = useStaticQuery(graphql`
  {
    backgroundImg: file(relativePath: { eq: "library-bg2.jpg" }) {
      publicURL
    }
  }`);
  return (
    <LibraryProvider {...props}>
      <div className={classnames("library-view", {mobile: matchMobile})}>
        <div className="library-view__cover" style={{
          backgroundImage: `url(${backgroundImg.publicURL})`,
          backgroundPosition: '50% 80%',
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover'
        }}>
          <div className="library-view__overlay" />
          <div className="library-view__cover-text">
            <h1>Biblioteca</h1>
            <p>Dejamos a tu alcance los documentos técnicos, de seguridad y algunos videos que sean de ayuda y te apoyen en la búsqueda de lo que necesitas.</p>
          </div>
        </div>
        <Grid container spacing={2} className="library-view__layout">
          <Grid item md={3} xs={12}>
            <SideBar {...props} />
          </Grid>
          <Grid item md={9} xs={12}>
            <Main {...props} />
          </Grid>
        </Grid>
      </div>
    </LibraryProvider>
  )
}

export default Biblioteca

