import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import Grid from "@material-ui/core/Grid"

const CatalogTable = ({ families, onSelectTDS }) => {
  let handleClick = catalog => () => {
    onSelectTDS(catalog)
  }
  return (
    <Grid container spacing={0} className="catalog-table">
      {families.map(family => (
        <Grid item xs={6} md={3}>
          <div className="table-container">
            <div className="table-head">Serie {family.name}</div>
            <div className="table-body">
              {family.products.map(product => (
                <button
                  className="btn-ghost catalog-name"
                  onClick={handleClick(product)}
                >
                  <span>{product.name}</span>
                  <span className="file"><FontAwesomeIcon
                    icon={["fas", "file-download"]}
                    style={{ color: "rgba(93, 93, 93, 0.69)" }}
                  /></span>
                </button>
              ))}
            </div>
          </div>
        </Grid>
      ))}
    </Grid>
  )
}

export default CatalogTable
