import React from "react"
import classnames from "classnames"
import useMediaQuery from "@material-ui/core/useMediaQuery"
import { useStaticQuery, graphql, Link } from "gatsby"
import take from 'lodash/take';

const NewsCard = props => {
  let { relevantNews } = useStaticQuery(graphql`
    {
      relevantNews: allStrapiNews(
        limit: 3
        filter: { destacada: { eq: true } }
      ) {
        edges {
          node {
            strapiId
            title
            content
            shortContent
            created_at(formatString: "MMMM Do, YYYY")
          }
        }
      }
    }
  `)
  let [newCards, setNewCards] = React.useState(relevantNews.edges)

  let matchesDesktop = useMediaQuery("(min-width:600px)")
  let matchesMobile = useMediaQuery("(max-width:599px)")

  let handleClickNextNew = () => setNewCards(sortCards(newCards))
  return (
    <div
      className={classnames("cards", {
        mobile: matchesMobile,
        desktop: matchesDesktop,
      })}
    >
      {newCards.map(({ node }, index) => (
        <div className="card">
          <h1 className="titlecard">
            {getTitle(node.title)}
          </h1>
          <p className="fechacard">{node.created_at}</p>
          <p className="textcard" dangerouslySetInnerHTML={{
            __html: node.shortContent
            }} />
            <Link to={`/noticias/${node.strapiId}`} className="btnNoticia">SEGUIR LEYENDO</Link>
            {
              index === 0 && <span onClick={handleClickNextNew} className="flechanext"></span>
            }
        </div>
      ))}
    </div>
  )
}

function sortCards(cards) {
  return [cards[1], cards[2], cards[0]];
}

function getTitle(title) {
  let lengthTitle = title.split(" ").length;
  if (lengthTitle > 6) return `${take(title.split(" "), 6).join(" ")}...`
  return title
}

export default NewsCard;