import React from "react"
import PropTypes from "prop-types"
import { makeStyles } from "@material-ui/core/styles"
import useMediaQuery from '@material-ui/core/useMediaQuery';
import TextField from "@material-ui/core/TextField"

const useStyles = makeStyles(theme => ({
  desktopinput: {
    marginTop: 8,
    marginBottom: 8,
    width: 'calc(100% - 280px)'
  },
  desktopcontainer: {
    display: 'flex',
    alignItems: 'center'
  },
  mobileinput: {
    marginTop: 8,
    marginBottom: 8,
    width: '100%'
  },
  mobilecontainer: {
    marginTop: 8,
    marginBottom: 8,
    display: 'block'
  }
}))

function StepFirst({ registerMethod, onSubmit }) {
  let matchesDesktop = useMediaQuery('(min-width:600px)');
  const classes = useStyles()
  return (
    <form onSubmit={onSubmit}>
      <label>Agrega una descripción</label>
      <div className={matchesDesktop ? classes.desktopcontainer : classes.mobilecontainer}>
        <TextField
          id="description"
          name="description"
          variant="outlined"
          label="Descripción*"
          className={matchesDesktop ? classes.desktopinput : classes.mobileinput}
          type="text"
          inputProps={{ref: registerMethod()}}
        />
        <button type="submit" className="btn btn-primary">
          Enviar
        </button>
      </div>
    </form>
  )
}

StepFirst.propTypes = {}

export default StepFirst
