import React from 'react';
import { Link } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Paper from '@material-ui/core/Paper';
import isEmpty from './is-empty';

let mapThNames = {
  resistencia: 'Resistencia',
  propiedades: 'Propiedades',
  viscosidad: 'Viscosidad (mPa-s)',
  torsion: 'Torsion de ruptura(N/m)',
  temperatura: 'Rango de temperatura de operación',
  apariencia: 'Apariencia',
  dureza: 'Dureza',
  condicionCurado: 'Condicion de curado',
  elongacion: 'Elongacion',
  packaging: 'Packaging',
  resistenciaCorte: 'Resistencia al corte',
  saltSpray: 'Salt Spray Test'
}


const ProductTableMobile = ({products, onSelectProduct}) => {
  let handleClick = (product) => () => {
    onSelectProduct(product);
  };
  return (
    <div className="products-table-container">
      {products.map(product => (
      <TableContainer component={Paper}>
        <Table onClick={handleClick(product)}>
          <TableHead>
            <TableRow>
              <TableCell>
                {product.name}
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              <TableCell>
                <p><b>Descripción</b>: {product.usages}</p>
                {!isEmpty(product.resistencia) && <p><b>Resistencia:</b> {product.resistencia}</p>}
                {!isEmpty(product.propiedades) && <p><b>Propiedades:</b> {product.propiedades}</p>}
                {!isEmpty(product.viscosidad) && <p><b>Viscosidad(mPa-s):</b> {product.viscosidad}</p>}
                {!isEmpty(product.torsion) && <p><b>Torsion de ruptura(N/m):</b> {product.torsion}</p>}
                {!isEmpty(product.temperatura) && (
                  <p><b>Rango de temperatura de operación:</b> {product.temperatura}</p>
                )}
                {!isEmpty(product.apariencia) && <p><b>Apariencia:</b> {product.apariencia}</p>}
                {!isEmpty(product.dureza) && <p><b>Dureza:</b> {product.dureza}</p>}
                {!isEmpty(product.condicionCurado) && <p><b>Condicion de curado:</b> {product.condicionCurado}</p>}
                {!isEmpty(product.elongacion) && <p><b>Elongacion:</b> {product.elongacion}</p>}
                {!isEmpty(product.packaging) && <p><b>Packaging:</b> {product.packaging}</p>}
                {!isEmpty(product.resistenciaCorte) && <p><b>Resistencia al corte:</b> {product.resistenciaCorte}</p>}
                {!isEmpty(product.saltSpray) && <p><b>Salt Spray Test:</b> {product.saltSpray}</p>}
                TDS/SDS <FontAwesomeIcon icon={["fas", "file-download"]} style={{color:"#000000"}} />


              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
      ))}
    </div>
  );
};

const ProductTable = ({ products, onSelectProduct }) => {
  let handleClick = (product) => () => {
    onSelectProduct(product);
  };

  let matchMobile = useMediaQuery('(max-width: 599px)')
  if (matchMobile) return <ProductTableMobile products={products} onSelectProduct={onSelectProduct} />
  let thNames = products.reduce((names, product) => {
    let __names = [];
    if (!isEmpty(product.resistencia) && !names.includes('resistencia')) __names.push('resistencia')
    if (!isEmpty(product.propiedades) && !names.includes('propiedades')) __names.push('propiedades');
    if (!isEmpty(product.viscosidad) && !names.includes('viscosidad')) __names.push('viscosidad');
    if (!isEmpty(product.torsion) && !names.includes('torsion')) __names.push('torsion')
    if (!isEmpty(product.temperatura) && !names.includes('temperatura')) __names.push('temperatura');
    if (!isEmpty(product.apariencia) && !names.includes('apariencia')) __names.push('apariencia');
    if (!isEmpty(product.dureza) && !names.includes('Dureza')) __names.push('Dureza')
    if (!isEmpty(product.condicionCurado) && !names.includes('condicionCurado')) __names.push('condicionCurado')
    if (!isEmpty(product.elongacion) && !names.includes('elongacion')) __names.push('elongacion')
    if (!isEmpty(product.packaging) && !names.includes('packaging')) __names.push('packaging')
    if (!isEmpty(product.resistenciaCorte) && !names.includes('resistenciaCorte')) __names.push('resistenciaCorte')
    if (!isEmpty(product.saltSpray) && !names.includes('saltSpray')) __names.push('saltSpray')
    return [...names, ...__names];
  }, []);
  console.log(products);
  return(
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Nombre</TableCell>
            <TableCell className="usages-cell">Descripción</TableCell>
            {thNames.map(thName => <TableCell>{mapThNames[thName]}</TableCell>)}
          </TableRow>
        </TableHead>
        <TableBody>
          {
            products.map(product => (
              <TableRow onClick={handleClick(product)}>
                <TableCell className="main-cell">
                  <img src={product.imageProduct.childImageSharp.fluid.src} alt="product-image" />
                  <div className="product-name">
                    {product.name}
                    <Link to={`/productos/serie/${product.family.name}`}>Serie {product.family.name}</Link>
                  </div>
                  <FontAwesomeIcon icon={["fas", "file-download"]} style={{color:"#000000"}} />
                </TableCell>
                <TableCell className="usages-cell">{product.usages}</TableCell>
                {
                  thNames.map(thName => <TableCell className="main-cell">{!isEmpty(product[thName]) ? product[thName] : '-'}</TableCell>)
                }
              </TableRow>
            ))
          }
        </TableBody>
      </Table>
    </TableContainer>
  )
};

export default ProductTable;
