import React from "react"
import { Link } from "gatsby"
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import CoverHero from "../../../components/cover-hero"
import VideoGallery from "../../../components/video-gallery"
import NavProducts from "../../../components/nav-products"
import Subtypes from "./subtypes"
import Grid from "@material-ui/core/Grid"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
// import PropTypes from 'prop-types';

function Products({ context, ...props }) {
  let { videoGallery, coverHeroes } = context
  let { pdfImage, backgroundImg } = useStaticQuery(graphql`
    {
      pdfImage: file(relativePath: { eq: "pdf-3-copy.png" }) {
        childImageSharp {
          fluid(maxWidth: 90) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      backgroundImg: file(relativePath: { eq: "shutterstock-85545598.png" }) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
    }
  `)
  return (
    <div className="home-page">
      <div className="product-view__cover" style={{backgroundImage: `url(${backgroundImg.childImageSharp.fluid.src})`}}>
        <div className="product-view__overlay" />
        <div className="library-view__cover-text">
          <h1>Líder en adhesivos y selladores.</h1>
          <p>Ofrecemos soluciones industriales, productos innovadores, servicio especializado y capacitación
de productos</p>
        </div>
      </div>

      <NavProducts />

      <Subtypes />



      <section className="products-documents">
        <h2 style={{'marginLeft': 40}}>También puedes ver</h2>
        <Grid container>
          <Grid item xs={12} md={4}>
            <Link to="/biblioteca/catalogos" className="btn-ghost doc-card">
              <FontAwesomeIcon icon={["fas", "file-pdf"]} style={{color: '#fff', width: 60, height: 80}} />
              <h2>Catálogo General de Productos</h2>
            </Link>
          </Grid>
          <Grid item xs={12} md={4}>
            <Link to="/biblioteca/tds" className="btn-ghost doc-card">
              <FontAwesomeIcon icon={["fas", "file-pdf"]} style={{color: '#fff', width: 60, height: 80}} />
              <h2>Hojas Técnicas por Producto</h2>
            </Link>
          </Grid>
          <Grid item xs={12} md={4}>
            <Link to="/biblioteca/sds" className="btn-ghost doc-card">
              <FontAwesomeIcon icon={["fas", "file-pdf"]} style={{color: '#fff', width: 60, height: 80}} />
              <h2>Hojas de Seguridad por Producto</h2>
            </Link>
          </Grid>
        </Grid>
      </section>
    </div>
  )
}

