import React from 'react';
import Product from '../product-view';
import Grid from '@material-ui/core/Grid';

let GridProducts = ({ products, onSelectProduct }) => {

  return (
    <div>
      <Grid container className="main-section__products">
        {
          products.map(product => (
            <Grid item md={4} xs={6}>
              <Product data={product} onClick={onSelectProduct}/>
            </Grid>
          ))
        }
      </Grid>
    </div>
  );
}



export default GridProducts;
