import React from 'react';

let NewsContext = React.createContext({});

let NewsProvider = (props) => {
  let getPageNews = () => props.context.news;
  let getNew = () => props.context.new;
  let pages = props.context.pages;
  let currentPage = props.context.currentPage;

  let contextValue = {
    getNew,
    getPageNews,
    pages,
    currentPage
  };
  return <NewsContext.Provider {...props} value={contextValue} />;
};

export let useNews = () => {
  let context = React.useContext(NewsContext);
  return context;
}

export default NewsProvider;
