import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

function Dots({ totalSteps, currentStep, goToStep }) {
  return (
    <div className="dots__container">
      <ul className="dots-list">
        {
          Array.from({length: totalSteps}, (v, index) => (
            <li className={classnames('dots-list__dot', { active: currentStep === index + 1 })}><button onClick={() => goToStep(index + 1)} /></li>
          ))
        }
      </ul>
    </div>
  )
}

Dots.propTypes = {

}

export default Dots;
