import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import get from 'lodash/get';
import map from 'lodash/map';
import reduce from 'lodash/reduce';
import find from 'lodash/find';
import sortMarkets from './markets-order';

let ProductState = React.createContext({});

let ProductsProvider = ({context, ...props}) => {
  let data = useStaticQuery(graphql`
  query Products {
    allStrapiProductSubtypes {
      edges {
        node {
          name
          title
          description
          slug
          strapiId
        }
      }
    }
    allStrapiMarkets(filter: {name: {nin: "None"}}) {
      edges {
        node {
          name
          slug
          title
          strapiId
          description
          applications {
            slug
            name
          }
        }
      }
    }
    allStrapiFamilies {
      edges {
        node {
          name
          title
          description
          slug
          strapiId
        }
      }
    }
    allStrapiProducts {
      edges {
        node {
          applications {
            slug
            id
          }
          markets {
            slug
            id
          }
          family {
            name
            id
          }
          product_subtype {
            id
          }
          imageProduct {
            publicURL
            childImageSharp {
              fluid(maxWidth: 960) {
                ...GatsbyImageSharpFluid
              }
            }
          }
          tdsProduct {
            publicURL
          }
          sdsProduct {
            publicURL
          }
          name
          usages
          viscosidad
          torsion
          temperatura
          resistencia
          propiedades
          apariencia
          dureza
          condicionCurado
          elongacion
          packaging
          saltSpray
          resistenciaCorte
        }
      }
    }
  }
  `);

  let getFromData = (node) => map(get(data, [node, 'edges']), ({node}) => node);
  let getSubtypes = () => getFromData('allStrapiProductSubtypes');
  let getMarkets = () => getFromData('allStrapiMarkets');
  let getFamilies = () => getFromData('allStrapiFamilies');
  let getProducts = () => getFromData('allStrapiProducts');

  let getMarketByPath = () => {
    if (context.isMarket) {
      return find(getMarkets(), market => market.strapiId === context.id);
    } else if (context.isApplication) {
      return find(getMarkets(), market => market.slug === context.market.slug);
    } else if (context.isSubtype) {
      return find(getSubtypes(), subtype => subtype.strapiId === context.id);
    } else if (context.isFamilyView) {
      return find(getFamilies(), family => family.strapiId === context.id);
    }
  }

  let getProductsByPath = () => {
    let findId = compareId => ({id}) => id === compareId;
    let products = getProducts();
    if (context.isMarket) return [];
    else if (context.isApplication) {
      return reduce(products, (filterProducts, product) => {
        if (find(product.applications, findId(context.id)) && find(product.markets, findId(context.market.id))) {
          return [...filterProducts, product];
        }
        return filterProducts;
      }, []);

    } else if (context.isSubtype) {
      return reduce(products, (filterProducts, product) => {
        if (get(product, 'product_subtype.id') === context.id) {
          return [...filterProducts, product];
        }
        return filterProducts;
      }, []);
    } else if (context.isFamilyView) {
      return reduce(products, (filterProducts, product) => {
        if (product.family && product.family.id === context.id) {
          return [...filterProducts, product];
        }
        return filterProducts;
      }, []);
    }
  }

  let contextValue = {
    getSubtypes,
    getMarkets: () => sortMarkets(getMarkets()),
    getFamilies,
    getProductsByPath,
    getMarketByPath
  };

  return (
    <ProductState.Provider {...props} value={contextValue}/>
  );
};

export let useProductState = () => {
  let context = React.useContext(ProductState);
  return context;
}

export default ProductsProvider;
