import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import Typography from "@material-ui/core/Typography"
import TextField from "@material-ui/core/TextField"
import { makeStyles } from "@material-ui/core/styles"
import useMediaQuery from "@material-ui/core/useMediaQuery"
import { useForm } from "react-hook-form"
import classnames from 'classnames';
import './modal-catalog.css'
import qs from 'querystring';
import axios from 'axios';
import Cookies from 'js-cookie';
import get from 'lodash/get';

const useStyles = makeStyles(theme => ({
  container: {
    paddingTop: 120,
    paddingBottom: 140,
    flexGrow: 1,
  },
  title: {
    marginBottom: theme.spacing(2),
    fontWeight: 500,
  },
  descripcion: {
    width: "80%",
    fontSize: 15,
    fontWeight: 500,
  },
  form: {
    width: "100%",
  },
  subtitulos: {
    fontSize: 15,
    fontWeight: "normal",
    fontStretch: "normal",
    fontStyle: "normal",
    lineHeight: 1.47,
    letterSpacing: "normal",
    color: "#000000",
    marginTop: 20,
    marginBottom: 0,
  },
  input: {
    marginTop: 8,
    marginBottom: 8,
  },
  select: {
    marginTop: 8,
    marginBottom: 8,
  },
  labelcheckbox: {
    fontSize: 14,
    marginBottom: 0,
  },
  link: {
    fontWeight: 600,
    color: "rgba(0, 0, 0, 0.87)",
  },
  submit: {
    borderRadius: 20,
    backgroundImage:
      "linear-gradient(0deg, rgba(50,53,64,1) 0%, rgba(181,181,181,1) 100%)",
    boxShadow: "0 7px 11px 0 rgba(0, 0, 0, 0.26)",
    width: 243,
    height: 40,
    color: "#ffffff",
    fontWeight: 500,
    letterSpacing: 4,
  },
}))

export default function ModalCatalog({ apiHostName, data, onClose, showContact }) {
  const classes = useStyles()
  let [modalShow, setModalShow] = React.useState(showContact ? 'contact' : 'form');
  const { handleSubmit, register, errors } = useForm()
  let matchMobile = useMediaQuery('(max-width: 599px)')
  let onSubmit = (values) => {
    axios.post(`${apiHostName}/api/cotiza`,
      qs.stringify({
        name: values.name,
        email: values.email,
        description: `Enviar TDS/SDS para ${data.data.name} `,
        novedades: 'NO'
      }), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((response) => {
        switch(data.flow) {
          case 'catalogos':
            return window.open(data.data.file, '_blank');
          case 'tds':
            Cookies.set('isRegisteredThreebond', true, { path: '' });
            let tdsSrc = get(data, 'data.tdsProduct.publicURL', false);
            if (!tdsSrc) {
              setModalShow('contact');
            } else {
              setModalShow('success');
              window.open(tdsSrc, '_blank');
            }
            return ;
          case 'sds':
            Cookies.set('isRegisteredThreebond', true, { path: '' });
            let sdsSrc = get(data, 'data.sdsProduct.publicURL', false);
            if (!sdsSrc) {
              setModalShow('contact');
            } else {
              setModalShow('success');
              window.open(sdsSrc, '_blank');
            }
            return
        }
      }).catch(error => {
        setModalShow('fail')
      })

  }
  return (
    <div className={classnames("catalog-modal", { mobile: matchMobile })}>
      <div className="close-modal-container">
        <button className="close-modal" onClick={onClose}>
          <FontAwesomeIcon
            icon={["fas", "times"]}
            style={{ color: "#000000" }}
          />
        </button>
      </div>
      <div className="modal-body">
        {
          modalShow === 'success' && <p>¡Muchas gracias por proporcionarnos tus datos! Alguien se pondra en contacto contigo.</p>
        }
        {
          modalShow === 'fail' && <p>Lo sentimos ha ocurrido un error. Intentelo más tarde.</p>
        }
        {
          modalShow === 'contact' && <p>Nos pondremos en contacto contigo para mandarte lo solicitado.</p>
        }
        {
          modalShow === 'form' && (
            <form onSubmit={handleSubmit(onSubmit)}>
              <Typography component="p" className={classes.subtitulos}>
                Dejános tu correo y te lo enviaremos a la brevedad
              </Typography>
              <TextField
                fullWidth
                id="name"
                label="Nombre*"
                name="name"
                autoComplete="name"
                type="text"
                className={classes.input}
                inputProps={{
                  maxLength: 30,
                  ref: register({
                    required: "Required",
                  }),
                }}
              />
              <TextField
                fullWidth
                id="email"
                label="Correo electrónico*"
                name="email"
                autoComplete="email"
                type="text"
                className={classes.input}
                inputProps={{
                  maxLength: 30,
                  ref: register({
                    required: "Required",
                  }),
                }}
              />
              <button className="btn btn-primary">Enviar</button>
            </form>
          )
        }
      </div>
    </div>
  )
}
