import React from 'react';
import PropTypes from 'prop-types';
import StepWizard from 'react-step-wizard';
import axios from 'axios';
import StepFirst from './step-first';
import StepTwo from './step-two';
import StepThree from './step-three';
import Dots from './dots';
import { useForm } from "react-hook-form"
import qs from 'querystring';
import Modal from 'react-modal';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

Modal.setAppElement('#___gatsby');
const modalStyles = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)",
    zIndex: 100
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "120px auto",
    padding: 0,
    border: 0,
    backgroundColor: '#fff'
  }
};
const modalStylesMobile = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)",
    zIndex: 100
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "20px auto",
    padding: 0,
    border: 0,
    backgroundColor: '#fff'
  }
};

function ContactUs(props) {
  const { handleSubmit, register, errors } = useForm()
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let [toggleModal, setToggleModal] = React.useState(false);
  let modalSuccess = React.useRef(true);
  let onClose = () => setToggleModal(false);
  let handleOnSubmit = (values) => {
    axios.post(`${props.context.apiHostName}/api/cotiza`,
      qs.stringify({
        name: values.name,
        email: values.email,
        description: values.description,
        novedades: 'NO'
      }), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((response) => {
      modalSuccess.current = true;
      setToggleModal(true);
    }).catch(error => {
      modalSuccess.current = false
      setToggleModal(true);
    })
  }
  return (
    <>
      <StepWizard className="step-wizzard" nav={<Dots />}>
        <StepFirst registerMethod={register} />
        <StepTwo registerMethod={register} />
        <StepThree registerMethod={register} onSubmit={handleSubmit(handleOnSubmit)}/>
      </StepWizard>
      <Modal
        isOpen={toggleModal}
        style={matchMobile ? modalStylesMobile : modalStyles}
        contentLabel="MODAL"
        className="cotiza-modal"
      >
        <div>
          <div className="close-modal-container">
            <button className="close-modal" onClick={onClose}>
              <FontAwesomeIcon
                icon={["fas", "times"]}
                style={{ color: "#000000" }}
              />
            </button>
          </div>
          {
            modalSuccess.current ?
              <p>¡Muchas gracias por proporcionarnos tus datos! Alguien se pondra en contacto contigo.</p> :
              <p>Lo sentimos ha ocurrido un error. Intentelo más tarde.</p>
          }

        </div>
      </Modal>
    </>
  )
}

ContactUs.propTypes = {

}

export default ContactUs

