import "date-fns"
import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import classnames from "classnames"
import NewsState, { useNews } from "../../../components/news-state"
import { makeStyles } from "@material-ui/core/styles"
import Container from "@material-ui/core/Container"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import DateFnsUtils from "@date-io/date-fns"
import useMediaQuery from "@material-ui/core/useMediaQuery"
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers"
import NewsCards from '../../../components/news-cards';
import "./news.css"

const useStyles = makeStyles(theme => ({
  container: {
    marginBottom: 80,
    flexGrow: 1,
  },
  newtitle: {
    fontSize: 30,
    fontWeight: "bold",
    color: "#000000",
    marginBottom: 8,
  },
  fecha: {
    fontSize: 17,
    color: "#000000",
    marginBottom: 10,
    lineHeight: 1.65,
  },
  contentnew: {
    fontSize: 15,
    lineHeight: 1.87,
    color: "#000000",
  },
  relatedtitle: {
    fontSize: 30,
    fontWeight: "bold",
    color: "#000000",
    marginBottom: 40,
    lineHeight: 1.37,
  },
  relatedsubtitle: {
    fontSize: 30,
    fontWeight: "bold",
    color: "#000000",
    marginBottom: 20,
    lineHeight: 1.37,
  },
  fecharelated: {
    fontSize: 17,
    lineHeight: 1.65,
    color: "#000000",
    marginBottom: 20,
  },
  textrelated: {
    fontSize: 15,
    lineHeight: 1.87,
    color: "#000000",
  },
}))

const News = () => {
  let { getPageNews, pages, currentPage } = useNews()
  let news = getPageNews() || []
  const classes = useStyles()
  return (
    <div className="containernoticias">
      <div>
        {news.map(__new => (
          <Grid container className="divnoticia">
            <Grid item xs={12} md={8}>
              <h1 className={classes.newtitle}>{__new.title}</h1>
              <p className={classes.fecha}>{__new.created_at}</p>
              <p dangerouslySetInnerHTML={{ __html: __new.shortContent }} />
            </Grid>
            <Grid item xs={12} md={4} className="divBtnNoticia">
              <Link to={`/noticias/${__new.strapiId}`} className="btnNoticia">
                SEGUIR LEYENDO
              </Link>
            </Grid>
          </Grid>
        ))}
      </div>
      <Paginacion pages={pages} current={currentPage}></Paginacion>
    </div>
  )
}

const Paginacion = props => {
  let arr = {}
  if (props.pages > 0) {
    arr[1] = ""
    arr[props.current] = ""
    arr[props.pages] = ""
    if (props.current - 1 > 0) arr[props.current - 1] = ""
    if (props.current + 1 < props.pages) arr[props.current + 1] = ""
  }
  return (
    <div className="paginationDiv">
      {Object.keys(arr).map(key => (
        <Link to={`/noticias/page/${key}`} className="paginacion">
          {key}
        </Link>
      ))}
    </div>
  )
}

const options = [
  {
    value: "0",
    label: "Categoría",
  },
  {
    value: "1",
    label: "CAT1",
  },
  {
    value: "2",
    label: "CAT2",
  },
  {
    value: "3",
    label: "CAT3",
  },
]

export default function App(props) {
  const data = useStaticQuery(graphql`
    {
      backgroundImg: file(relativePath: { eq: "news-bg3.jpg" }) {
        publicURL
      }
    }
  `);

  const classes = useStyles()
  const [selectedDate, setSelectedDate] = React.useState(new Date())
  let matchesMobile = useMediaQuery("(max-width:599px)")
  const handleDateChange = date => {
    setSelectedDate(date)
    document.getElementsByClassName("fechaselected")[0].textContent =
      date.getDate() + "/" + (date.getMonth() + 1) + "/" + date.getFullYear()
  }
  const handleClickFecha = function() {
    document.getElementsByClassName("MuiIconButton-root")[0].click()
  }
  return (
    <div>
      <div className={classnames('top', {mobile: matchesMobile})} style={{backgroundImage: `url(${data.backgroundImg.publicURL})`}}>
        <div className="new-bg" />
        <div style={{position: 'relative', width: '500px'}}>
          <div style={{position: 'absolute', width: '100%', left: 0}} >
            <h1 className="titulopagina">
              NOTICIAS
              <br></br>
              DESTACADAS
            </h1>
          </div>
        </div>
        <NewsCards />
      </div>
      <Container maxWidth="xl" className="containerFilter">
        
      </Container>
      <Container maxWidth="lg" className={classes.container}>
        <CssBaseline />
        <NewsState {...props}>
          <News />
        </NewsState>
      </Container>
    </div>
  )
}

