import React from "react"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import { makeStyles } from "@material-ui/core/styles"
import Container from "@material-ui/core/Container"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

const useStyles = makeStyles(theme => ({
  container: {
    paddingTop: 110,
    paddingBottom: 250,
    flexGrow: 1
  },
  title: {
    fontSize: 43,
    fontWeight: 500,
    lineHeight: 0.78,
    color: "#000000",
  },
  h2: {
    fontSize: 27,
    fontWeight: "bold",
    lineHeight: 1.4,
    color: "#030c19",
    marginTop: 0,
    marginBottom: 30,
  },
  h3: {
    fontSize: 22,
    fontWeight: "bold",
    lineHeight: 1.1,
    color: "#000000",
    marginTop: 60,
    marginBottom: 60,
  },
  h4: {
    fontSize: 18,
    fontWeight: "normal",
    lineHeight: 1.32,
    color: "#000000",
    marginTop: 40,
    marginBottom: 40,
  },
  h5: {
    fontSize: 14,
    fontWeight: "normal",
    lineHeight: 1.83,
    color: "#000000",
    marginTop: 40,
  },
  p: {
    lineHeight: '1.7'
  }
}))

export default function Nosotros() {
  const classes = useStyles()
  const {
    historyImg,
    historyImg2,
    historyImg3,
    historyImg4
  } = useStaticQuery(graphql`{
    historyImg: file(relativePath: { eq: "historia.png" }) {
      childImageSharp {
        fluid(maxWidth: 210, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }

    historyImg2: file(relativePath: { eq: "shutterstock-1722289441.png" }) {
      childImageSharp {
        fluid(maxWidth: 210, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }

    historyImg3: file(relativePath: { eq: "shutterstock-1341621296.png" }) {
      childImageSharp {
        fluid(maxWidth: 210, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }

    historyImg4: file(relativePath: { eq: "shutterstock-1119927341.png" }) {
      childImageSharp {
        fluid(maxWidth: 210, quality: 100) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }`);

  return (
    <Container component="main" maxWidth="lg" className={classes.container}>
      <CssBaseline />
      <h1 className={classes.title}>Acerca de Three Bond</h1>
      <Grid container spacing={0}>
        <Grid item md={3} className="history-line">
          <div>
            <Img fluid={historyImg.childImageSharp.fluid} style={{width: 179}} />
          </div>
        </Grid>
        <Grid item xs={12} md={9}>
          <h2 className={classes.h2}>iCreando nuestro futuro desde una sola gota!</h2>
          <p className={classes.p}>
            Este ideal surge de una historia simple contada por primera vez por nuestro fundador, el Sr. Osamu Ukumori, hace poco más de 60 años.<br/><br/>
            En un cálido día de verano en Tokio, observó un pequeño punto negro y brillante en la carretera, donde un auto acababa de detenerse momentos antes. Era una mezcla de aceite y agua que se había filtrado debajo del vehículo y había creado un área brillante y resbaladiza en el asfalto. Durante la primera década después de la Segunda Guerra Mundial, el Sr. Ukomori fue dolorosamente consciente de los preciosos y limitados recursos naturales de Japón. Incluso era consciente de los riesgos de seguridad que tales manchas resbaladizas podrían causar a otros. Y luego estaba la cuestión de ayudar a reconstruir un nuevo Japón, haciendo una contribución que fuera más grande que sus propias necesidades e intereses personales, y que sirviera a su país.<br/><br/>
            Los productos de ThreeBond ahora se utilizan en una amplia variedad de aplicaciones. Se utilizan en automóviles y otros vehículos, equipos de transporte, materiales de construcción públicos, materiales de construcción, equipos eléctricos y electrónicos, y altas tecnologías. Los productos ThreeBond se han convertido en una necesidad en los procesos de producción de varios productos en muchas áreas. Si bien el público en general puede no ver los productos ThreeBond en su vida cotidiana, hemos asegurado la posición número uno en selladores y adhesivos industriales, y actualmente tenemos sistemas de producción y ventas en Japón, América del Norte y Central, América del Sur, Europa, Asia y China. Estamos orgullosos de ser una empresa en la que confían nuestros clientes.<br/><br/>
            Para un país como Japón, donde los recursos naturales son escasos, las "fugas" como la fuga de petróleo y la fuga de agua conducen a la pérdida de energía. El deseo de evitar que nuestra preciosa energía se filtre es la base de la filosofía de ThreeBond desde su inicio.<br/><br/>
          </p>
        </Grid>
      </Grid>
      <Grid container spacing={0}>
        <Grid item md={3} className="history-line">
          <div>
            <Img fluid={historyImg2.childImageSharp.fluid} style={{width: 179}} />
          </div>
        </Grid>
        <Grid item xs={12} md={9}>
          <h2 className={classes.h2}>Filosofía Three Bond.</h2>
          <p className={classes.p}>
            Principios Fundacionales.<br /><br />
            <ol>
              <li>Manifestar amor a través del trabajo.</li>
              <li>Servir al ser humano y a la sociedad a través del trabajo.</li>
              <li>Luchar por el crecimiento espiritual y el desarrollo a través del trabajo.</li>
            </ol>
          </p>
        </Grid>
      </Grid>
      <Grid container spacing={0}>
        <Grid item md={3} className="history-line">
          <div>
            <Img fluid={historyImg3.childImageSharp.fluid} style={{width: 179}} />
          </div>
        </Grid>
        <Grid item xs={12} md={9}>
          <h2 className={classes.h2}>Espíritu de la empresa:</h2>
          <p className={classes.p}>
          El lugar de trabajo no es un lugar para el individualismo ni el totalitarismo. Más bien, es un lugar donde cada uno puede vivir y crecer según su propia comprensión del espíritu de cooperación.
          </p>
        </Grid>
      </Grid>
      <Grid container spacing={0}>
        <Grid item md={3} className="history-line">
          <div>
            <Img fluid={historyImg4.childImageSharp.fluid} style={{width: 179}} />
          </div>
        </Grid>
        <Grid item xs={12} md={9}>
          <h2 className={classes.h2}>Credo operacional.</h2>
          <p className={classes.p}>
            Decidimos promover el avance técnico del mundo industrial a través de la plena utilización de la tecnología, los productos y la organización de nuestra empresa. Además, decidimos operar de acuerdo con el siguiente credo para poder contribuir a la evolución de la cultura humana lo mejor que podamos:
            <br/><br />
            <ol>
              <li>Servir con sincero entusiasmo.</li>
              <li>Sinceridad y buena voluntad en todo momento.</li>
              <li>Respeta lo absoluto del tiempo.</li>
            </ol>
            <br/>
            El "credo operativo", el "espíritu de la empresa" y los "principios fundacionales" en la filosofía de gestión de ThreeBond son la base de ThreeBond junto con el objetivo fundador de esta empresa, que era evitar la fuga de energía preciosa. Hasta el día de hoy, nos enorgullecemos de nuestra "filosofía de gestión" y la idea única que inició esta empresa, y seguimos produciendo productos que satisfacen las necesidades de nuestros clientes trabajando siempre de la mejor manera posible con un fuerte unísono con nuestro personal.
          </p>
        </Grid>
      </Grid>
    </Container>
  )
}
