import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import Modal from "react-modal"
import get from "lodash/get"
import isEmpty from "lodash/isEmpty";

import { useMeta } from "../../../../../components/layout/layout";
import VideoGallery from "../../../../../components/video-gallery"
import { useProductState } from "../products-state"
import GridProducts from "../grid-products"
import TableProducts from "./product-table"
import ModalProduct from "./modal-product"
import ModalCatalog from "../../../biblioteca/components/main-section/modal-catalog"
import useMediaQuery from '@material-ui/core/useMediaQuery';

Modal.setAppElement("#___gatsby")
const modalStyles = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)",
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "960px",
    margin: "100px auto",
    padding: 0,
    border: 0,
    width: '800px'
  },
}
const modalStylesMobile = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.58)",
  },
  content: {
    position: "relative",
    top: "auto",
    left: "auto",
    right: "auto",
    bottom: "auto",
    maxWidth: "100vw",
    margin: "0",
    padding: 0,
    border: 0,
  },
}

let Main = ({ path, context }) => {
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let { marketImg } = useStaticQuery(graphql`
    {
      marketImg: file(relativePath: { eq: "markets-default.jpg" }) {
        childImageSharp {
          fluid {
            src
          }
        }
      }
    }
  `);

  let { getProductsByPath, getMarketByPath } = useProductState()
  let [gridView, setGridView] = React.useState(true)
  let market = getMarketByPath()
  let products = getProductsByPath()
  let { setMetaData } = useMeta();
  setMetaData(market);

  let [toggleModal, setToggleModal] = React.useState(false)
  let [showCatalog, setShowCatalog] = React.useState(false)
  let productInModal = React.useRef(null)

  let openModalWProduct = product => {
    setToggleModal(true)
    productInModal.current = product
  }

  let closeModal = () => {
    setToggleModal(false)
    setShowCatalog(false)
    productInModal.current = null
  }

  let handleDownloadTDS = () => {
    setShowCatalog(true)
  }

  let toggleView = () => setGridView(!gridView)
  let videoGallery =
    get(context, "videoGallery") || get(context, "market.videoGallery")

  let ModalComponent = null
  if (toggleModal && !showCatalog) {
    ModalComponent = (
      <ModalProduct
        product={productInModal.current}
        onClose={closeModal}
        onDownloadTDS={handleDownloadTDS}
      />
    )
  } else if (toggleModal && showCatalog) {
    ModalComponent = (
      <ModalCatalog
        data={{
          data: {
            ...productInModal.current,
          },
          flow: "tds-sds",
        }}
        onClose={closeModal}
      />
    )
  }

  return (
    <section className="product-view__main-section">
      <h1>{market.title}</h1>
      {!isEmpty(videoGallery) ? <VideoGallery gallery={videoGallery} /> : <img src={marketImg.childImageSharp.fluid.src} />}
      <p>{market.description}</p>
      {market.applications ? (
        <>
          <h2>Selecciona una aplicación</h2>
          <div className="main-section__apps">
            {market.applications &&
              market.applications.map(app => (
                <Link
                  id={app.slug}
                  to={`/productos/${market.slug}/${app.slug}#${app.slug}`}
                >
                  {app.name}
                </Link>
              ))}
          </div>
        </>
      ) : null}
      {products.length > 0 ? (
        <>
          <div className="align-right">
            <br />
            Vista:&nbsp;
            <button className="btn-ghost" onClick={toggleView}>
              {gridView ? (
                <FontAwesomeIcon
                  icon={["fas", "border-all"]}
                  style={{ color: "#000000" }}
                />
              ) : (
                <FontAwesomeIcon
                  icon={["fas", "th"]}
                  style={{ color: "#000000" }}
                />
              )}
            </button>
          </div>
          {!gridView ? (
            <TableProducts
              onSelectProduct={openModalWProduct}
              products={products}
            />
          ) : (
            <GridProducts
              onSelectProduct={openModalWProduct}
              products={products}
            />
          )}
        </>
      ) : null}
      <Modal isOpen={toggleModal} style={matchMobile ? modalStylesMobile : modalStyles} contentLabel="MODAL">
        {productInModal.current ? ModalComponent : null}
      </Modal>
    </section>
  )
}

export default Main