import React from 'react';
import { graphql, useStaticQuery, Link } from 'gatsby';
import get from 'lodash/get';
import map from 'lodash/map';
import sortBy from 'lodash/sortBy';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames';

const order = ['adhesivos', 'selladores', 'coating', 'limpiadores'];

let Subtypes = () => {
  let data = useStaticQuery(graphql`
  {
    allStrapiProductSubtypes {
      edges {
        node {
          imageProductSubtype {
            publicURL
          }
          name
          slug
        }
      }
    }
  }
  `);
  let sorted =  sortBy(data.allStrapiProductSubtypes.edges, ({node}) => order.indexOf(node.slug));

  let matchMobile = useMediaQuery('(max-width: 599px)')
  return (
    <section className={classnames("find-products-as", { mobile: matchMobile })}>
      <h1>También encuéntralos como</h1>
        <div className="inline-list">
          {
            map(
              sortBy(sorted, ({node}) => order.indexOf(node.slug)),
              (({node}) => (
              <Link className="sub-type" to={`/productos/${node.slug}`}>
                <img src={get(node, 'imageProductSubtype.publicURL')} />
                <div>
                  {node.name}
                </div>
              </Link>
            )))
          }
      </div>
    </section>
  )
}

export default Subtypes;
