import React from 'react';
import classnames from 'classnames';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTranslation } from "react-i18next"

import CoverHero from '../../../components/cover-hero';
import VideoGallery from '../../../components/video-gallery';
import NewsSection from '../../../components/news-section';
import ContactUs from '../../../components/contact-us-wizzard';
import { Link, useStaticQuery, graphql } from 'gatsby';

let getBackgroundStyle = (img) => {
  return {
    width: '100vw',
    height: '90vh'
  }
}

const HomePage = ({ context, ...props }) => {
  let matches = useMediaQuery('(min-width:600px)');
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let { videoGallery } = context;

  let {backgroundImg} = useStaticQuery(graphql`
  {
    backgroundImg: file(relativePath: { eq: "group-8.png" }) {
      publicURL
    }
  }`);

  return (
    <div className="home-page">
      <section className={classnames("cover-hero", {mobile: matchMobile})}>
        <div className="cover-hero__item" >
          <img src={backgroundImg.publicURL} style={getBackgroundStyle()} />
          <div className="cover-hero__content home-cover">
            <h1>Crenado nuestro futuro desde una simple gota.</h1>
            <p>Tecnología Japonesa de calidad mundial.</p>
            <Link to="/nosotros" className="btn btn-secondary">CONOCE MÁS</Link>
          </div>
        </div>
      </section>

      <section className="first-section">
        <div className={classnames("section-container", {desktop: matches})}>
          <p>Los productos de ThreeBond® ahora se utilizan en una amplia variedad de aplicaciones. Se utilizan en automóviles y otros vehículos, equipos de transporte, materiales de construcción públicos, materiales de construcción, equipos eléctricos y electrónicos, y altas tecnologías. Los productos ThreeBond® se han convertido en una necesidad en los procesos de producción de varios productos en muchas áreas.</p>
          <div>
            <h2>¿Qué estás buscando?</h2>
            <table>
              <tr>
                <td><button className="solutions">SOLUCIONES PARA PyMes</button></td>
                <td><button className="solutions">SOLUCIONES PARA LA INDUSTRIA</button></td>
              </tr>
            </table>
            <Link to="/productos" className="btn btn-primary center-element">Ver todos</Link>
          </div>
        </div>
      </section>

      <VideoGallery gallery={videoGallery} />

      <NewsSection />

      <section className="cotizar-section">
        <div className="section-container">
          <h2>¿Tienes dudas, requieres información o alguna asesoria? </h2>
          <ContactUs context={context} />
        </div>
      </section>
    </div>
  );
};

export default HomePage;
