import React from 'react';
import NavProducts from '../../../components/nav-products';
import SideBar from './components/side-bar';
import ProductState from './components/products-state';
import Main from './components/main-section';
import Grid from '@material-ui/core/Grid';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames';
import { useStaticQuery, graphql } from 'gatsby';

let ProductViews = (props) => {
  let matchMobile = useMediaQuery('(max-width: 599px)');
  let {backgroundImg} = useStaticQuery(graphql`
  {
    backgroundImg: file(relativePath: { eq: "shutterstock-85545598.png" }) {
      childImageSharp {
        fluid {
          src
        }
      }
    }
  }`);
  return (
    <ProductState {...props}>
      <div className={classnames("product-view", {mobile: matchMobile})}>
        <div className="product-view__cover" style={{backgroundImage: `url(${backgroundImg.childImageSharp.fluid.src})`}}>
          <div className="product-view__overlay" />
        </div>
        <NavProducts />
        <Grid container spacing={2} className="product-view__layout">
          <Grid item xs={12} sm={3} className="product-view__layout-side-bar">
            <SideBar {...props} />
          </Grid>
          <Grid item xs={12} sm={9} className="product-view__layout-side-bar">
            <Main {...props} />
          </Grid>
        </Grid>
      </div>
    </ProductState>
  )
};

export default ProductViews;

