import React from "react"
import NewsState, { useNews } from "../../../components/news-state"
import { Link, useStaticQuery, graphql } from "gatsby"
import { makeStyles } from "@material-ui/core/styles"
import Container from "@material-ui/core/Container"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import "./news.css"

const useStyles = makeStyles(theme => ({
  containersup: {
    paddingTop: 65,
    paddingBottom: 40,
    flexGrow: 1,
  },
  container: {
    paddingTop: 0,
    paddingBottom: 80,
    flexGrow: 1,
  },
  news: {
    paddingBottom: 120,
  },
  newtitle: {
    fontSize: 60,
    fontWeight: 500,
    color: "#000000",
    marginBottom: 40,
  },
  subtitles: {
    fontSize: 15,
    lineHeight: 0.8,
    letterSpacing: "normal",
    color: "#000000",
    marginBottom: 10,
  },
  titleblock: {
    marginBottom: 30,
  },
  relatedtitle: {
    fontSize: 30,
    fontWeight: "bold",
    color: "#000000",
    marginBottom: 40,
    lineHeight: 1.37,
  },
  relatedsubtitle: {
    fontSize: 30,
    fontWeight: "bold",
    color: "#000000",
    marginBottom: 20,
    lineHeight: 1.37,
  },
  fecharelated: {
    fontSize: 17,
    lineHeight: 1.65,
    color: "#000000",
    marginBottom: 20,
  },
  textrelated: {
    fontSize: 15,
    lineHeight: 1.87,
    color: "#000000",
  },
}))

const NewsView = () => {
  let { getNew } = useNews()
  let __new = getNew()
  const classes = useStyles()
  return (
    <div>
      <Grid container className={classes.news}>
        <Grid item xs={12} className={classes.titleblock}>
          <h1 className={classes.newtitle}>{__new.title}</h1>
          <p className={classes.subtitles}>{__new.created_at}</p>
          <p className={classes.subtitles}>ThreeBond Co., Ltd.</p>
          <p className={classes.subtitles}>
            {__new.new_categories.map(x => x.categoryName).join(", ")}
          </p>
        </Grid>
        <Grid item xs={12}>
          <p dangerouslySetInnerHTML={{ __html: __new.content }} />
        </Grid>
      </Grid>
    </div>
  )
}

const App = props => {
  let { relevantNews } = useStaticQuery(graphql`
    {
      relevantNews: allStrapiNews(
        limit: 3
        filter: { destacada: { eq: true } }
      ) {
        edges {
          node {
            strapiId
            title
            content
            shortContent
            created_at(formatString: "MMMM Do, YYYY")
          }
        }
      }
    }
  `)
  const classes = useStyles()
  return (
    <div>
      <Container
        component="main"
        maxWidth="xl"
        className={classes.containersup}
      >
        <CssBaseline />
        <Grid container>
          <Grid item xs={12}>
            <Link
              role="button"
              className="btnRegresar"
              to="/noticias"
            >
              &lt;&nbsp;Regresar a Noticias
            </Link>
          </Grid>
        </Grid>
      </Container>
      <Container component="main" maxWidth="lg" className={classes.container}>
        <CssBaseline />
        <NewsState {...props}>
          <NewsView />
        </NewsState>
        <Grid container>
          <Grid item xs={12}>
            <h1 className={classes.relatedtitle}>También te puede interesar</h1>
          </Grid>
        </Grid>
        <Grid container spacing={5}>
          {
            relevantNews.edges.map(({node}) => (
              <Grid item xs={12} lg={4}>
                <h1 className={classes.relatedsubtitle}>
                  {node.title}
                </h1>
                <p className={classes.fecharelated}>{node.created_at}</p>
                <p className={classes.textrelated} dangerouslySetInnerHTML={{__html: node.shortContent}} />
                <Link to={`/noticias/${node.strapiId}`} className="btnNoticia">SEGUIR LEYENDO</Link>
              </Grid>
            ))
          }
        </Grid>
      </Container>
    </div>
  )
}

export default App
