import React from 'react';

let Catalog = (props) => {
  let handleClick = () => props.onClick(props.data);
  return (
    <button className="btn-ghost catalog-item" onClick={handleClick}>
      <img src={props.data.thumb} alt={props.data.name}/>
      <h3>{props.data.name}</h3>
      <p className="catalog-item__usages">{props.data.description}</p>
    </button>
  )
};

export default Catalog;
