import React from "react"
import PropTypes from "prop-types"
import useMediaQuery from '@material-ui/core/useMediaQuery';
import classnames from 'classnames';
import Cards from '../../components/news-cards';
import { Link, useStaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"

function NewsSection(props) {
  let matchesDesktop = useMediaQuery('(min-width:600px)');
  let matchesMobile = useMediaQuery('(max-width:599px)');
  let { newBg } = useStaticQuery(graphql`
    {
      newBg: file(relativePath: { eq: "news-bg2.jpg" }) {
        publicURL
      }
    }
  `);
  return (
    <section
      className={classnames({
        mobile: matchesMobile,
        desktop: matchesDesktop
      })}
      id="news-section"
      style={{
        backgroundPosition: '50% 50%',
        backgroundSize: 'cover',
        backgroundImage: `url(${newBg.publicURL})`
      }}
    >
      <div className="new-bg" />
      <div className="news-section__container">
        <div className="news-section__left">
          <div style={{position: 'absolute', width: '100%'}}>
            <h1>Noticias técnicas</h1>
            <p>
            Somos parte de una industria que esta siempre en movimiento, por lo cual el intercambio de información es vital para mantenernos informados y crear herramientas que nos permitan entender y reaccionar en momentos importantes. Es por eso que nos comprometemos en compartir semanalmente noticias, consejos de negociaciones y novedades de la industria para así crecer junto a ti y tu negocio, departamento o empresa.
            </p>
          </div>
        </div>
        <div className="news-section__right">
          <Cards />
        </div>
      </div>
      <div style={{height: '87px'}}>
        <div style={{position: 'absolute', width: '100%', left: 0}} >
          <Link to="/noticias" className="btn btn-primary see-all">Ver todos</Link>
        </div>
      </div>
    </section>
  )
}

NewsSection.propTypes = {}

export default NewsSection
