import React from 'react';
import { Link } from 'gatsby';

let SideBar = (props) => {
  return (
    <section className="catalog__side-bar">
      <SideLink title='CATALOGOS' href={`/biblioteca/catalogos`} />
      <SideLink title='Hojas Técnicas por Producto' href={`/biblioteca/tds`}/>
      <SideLink title='Hojas de Seguridad por Producto' href={`/biblioteca/sds`}/>
    </section>
  );
}

function SideLink({href, title}) {
  return (
    <div className="toggle-panel">
      <h2 className="toggle-panel__title">
        <Link to={href}>{title}</Link>
      </h2>
    </div>
  )
}

export default SideBar;

