import React from 'react';
import classnames from 'classnames';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"

let TogglePanel = ({ title, children, collapsed }) => {
  let [__collapse, setCollapse] = React.useState(collapsed);

  let panelClassnames = classnames('toggle-panel__panel', {
    collapse: __collapse,
  });

  let toggleCollapse = () => setCollapse(!__collapse);

  React.useEffect(() => {
    setCollapse(collapsed);
  }, [collapsed])
  return (
    <div className="toggle-panel">
      <h2 className="toggle-panel__title">
        <button className="btn-ghost" onClick={toggleCollapse}>
          {title}&nbsp;&nbsp;
          {
            __collapse ?
            <FontAwesomeIcon icon={["fas", "chevron-down"]} style={{color:"#7e7e7e"}} />
            : <FontAwesomeIcon icon={["fas", "chevron-up"]} style={{color:"#7e7e7e"}} />
          }
        </button>
      </h2>
      <div className={panelClassnames}>{children}</div>
    </div>
  );
};

export default TogglePanel;

