import React from 'react';
import { graphql, useStaticQuery, Link } from 'gatsby';
import get from 'lodash/get';
import map from 'lodash/map'
import useMediaQuery from "@material-ui/core/useMediaQuery"
import flatMap from 'lodash/flatMap';
import classnames from 'classnames';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import sortMarkets from '../../templates/views/product-views/components/products-state/markets-order';

let reducerSubNav = (state, action) => {
  switch(action.type) {
    case 'ALL':
      return {industry: false, home: false, all: !state.all};
    case 'INDUSTRY':
      return {home: false, all: false, industry: !state.industry};
    case 'HOME':
      return {industry: false, all: false, home: !state.home};
    case 'CLOSE':
      return {all: false, industry: false, home: false};
 }
}

function NavProducts() {
  let data = useStaticQuery(graphql`
    query NavProducts {
      allStrapiNavProducts {
        edges {
          node {
            slug
            name
            products_categories {
              category
            }
          }
        }
      }
    }
  `);
  let matchesMobile = useMediaQuery("(max-width:599px)");

  let [openSubNavigation, setOpenSubNavigation] = React.useReducer(reducerSubNav, { all: false, industry: false, home: false });

  let subNavigationAllClassnames = classnames('sub-navigation', { active: openSubNavigation.all, disabled: !openSubNavigation.all });
  let subNavigationIndustryClassnames = classnames('sub-navigation', { active: openSubNavigation.industry, disabled: !openSubNavigation.industry });
  let subNavigationHomeClassnames = classnames('sub-navigation', { active: openSubNavigation.home, disabled: !openSubNavigation.home });

  let navigation = sortMarkets(map(get(data, 'allStrapiNavProducts.edges', []), ({node}) => node));

  let toggleSubNav = (type) => (event) => {
    event.stopPropagation();
    setOpenSubNavigation({ type });
  };

  React.useEffect(() => {
    let closeSubNav = (event) => {
      if (openSubNavigation.all || openSubNavigation.industry || openSubNavigation.home) {
        setOpenSubNavigation({ type: 'CLOSE' });
      }
    };
    window.addEventListener('click', closeSubNav, false);
    return () => window.removeEventListener('click', closeSubNav);
  })

  return(
    <section className={classnames("nav-products", { mobile: matchesMobile })}>
      <div className="nav-products__card">
        <h1>¿Para qué mercado estás buscando?</h1>
        <nav>
          <ul className="nav-products__main-navigation inline-list">
            <li>
              <button onClick={toggleSubNav('ALL')}>
                Ver todos&nbsp;&nbsp;
                {
                  !openSubNavigation.all ?
                  <FontAwesomeIcon icon={["fas", "chevron-down"]} style={{color:"#7e7e7e"}} />
                  : <FontAwesomeIcon icon={["fas", "chevron-up"]} style={{color:"#7e7e7e"}} />
                }
              </button>
              <ul className={subNavigationAllClassnames}>
                {
                  navigation.map(node => {
                    return (
                      <li><Link to={`/productos/${node.slug}`}>{node.name}</Link></li>
                    );
                  })
                }
              </ul>
            </li>
            <li>
              <button onClick={toggleSubNav('INDUSTRY')}>
                Para la industria&nbsp;&nbsp;
                {
                  !openSubNavigation.industry ?
                  <FontAwesomeIcon icon={["fas", "chevron-down"]} style={{color:"#7e7e7e"}} />
                  : <FontAwesomeIcon icon={["fas", "chevron-up"]} style={{color:"#7e7e7e"}} />
                }
              </button>
              <ul className={subNavigationIndustryClassnames}>
                {
                  getNavsByCategory('industry', navigation)
                }
              </ul>
            </li>
            <li>
              <button onClick={toggleSubNav('HOME')}>
                Soluciones para PyMes&nbsp;&nbsp;
                {
                  !openSubNavigation.home ?
                  <FontAwesomeIcon icon={["fas", "chevron-down"]} style={{color:"#7e7e7e"}} />
                  : <FontAwesomeIcon icon={["fas", "chevron-up"]} style={{color:"#7e7e7e"}} />
                }
              </button>
              <ul className={subNavigationHomeClassnames}>
                {
                  getNavsByCategory('home', navigation)
                }
              </ul>
            </li>
          </ul>
        </nav>
      </div>
    </section>
  )
}

function getNavsByCategory(category, navigation) {
  return navigation.reduce((nodes, {node}) => {
    let categories = flatMap(
      get(node, 'products_categories', []),
      (c) => get(c, 'category')
    );
    if (!categories.includes(category)) {
      return nodes;
    }
    let reactNode = <li><Link to={`/productos/${node.slug}`}>{node.name}</Link></li>;
    return [...nodes, reactNode];
  }, [])
}

export default NavProducts;

