import React from 'react';
import Catalog from '../catalog-view';
import Grid from '@material-ui/core/Grid';

let GridCatalogs = ({ catalogs, onSelectCatalog }) => {

  return (
    <div>
      <Grid container className="main-section__catalogs">
        {
          catalogs.map(catalog => (
            <Grid item md={3} xs={6}>
              <Catalog data={catalog} onClick={onSelectCatalog}/>
            </Grid>
          ))
        }
      </Grid>
    </div>
  );
}



export default GridCatalogs;
