import React from "react"
import PropTypes from "prop-types"
import { makeStyles } from "@material-ui/core/styles"
import useMediaQuery from '@material-ui/core/useMediaQuery';
import TextField from "@material-ui/core/TextField"

const useStyles = makeStyles(theme => ({
  desktopinput: {
    marginTop: 8,
    marginBottom: 8,
    width: 'calc(100% - 280px)'
  },
  desktopcontainer: {
    display: 'flex',
    alignItems: 'center'
  },
  mobileinput: {
    marginTop: 8,
    marginBottom: 8,
    width: '100%'
  },
  mobilecontainer: {
    marginTop: 8,
    marginBottom: 8,
    display: 'block'
  }
}))

function StepFirst({ nextStep, registerMethod }) {
  let matchesDesktop = useMediaQuery('(min-width:600px)');
  const classes = useStyles()
  return (
    <div>
      <label>Comienza por capturar tu nombre completo</label>
      <div className={matchesDesktop ? classes.desktopcontainer : classes.mobilecontainer}>
        <TextField
          id="name"
          name="name"
          variant="outlined"
          label="Nombre completo*"
          className={matchesDesktop ? classes.desktopinput : classes.mobileinput}
          autoComplete="name"
          type="text"
          inputProps={{ref: registerMethod()}}
        />
        <button className="btn btn-primary" onClick={nextStep}>
          Siguiente
        </button>
      </div>
    </div>
  )
}

StepFirst.propTypes = {}

export default StepFirst
