import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import get from 'lodash/get';
import reduce from 'lodash/reduce';
import find from 'lodash/find';
import isNil from 'lodash/isNil';

let LibraryState = React.createContext({});

let LibrarysProvider = ({context, ...props}) => {
  let data = useStaticQuery(graphql`
  query Librarys {
    allStrapiFamilies {
      edges {
        node {
          name
          products {
            name
            tdsProduct {
              publicURL
            }
            sdsProduct {
              publicURL
            }
          }
        }
      }
    }
    allStrapiVideoGalleries {
      edges {
        node {
          header
          description
          source
          thumbVideoGallery {
            childImageSharp {
              fluid {
                src
              }
            }
          }
        }
      }
    }
    allStrapiCatalogs {
      edges {
        node {
          strapiId
          name
          description
          fileCatalog {
            publicURL
          }
          thumbCatalog {
            childImageSharp {
              fluid {
                src
              }
            }
          }
        }
      }
    }
  }
  `);

  let getFromData = (node, cb) => reduce(get(data, [node, 'edges']), (data, {node}) => cb ? cb(data, node) : [...data, node], []);

  let getDocs = (products, key) => reduce(products, (data, product) => {
    if (!isNil(product[key])) return [...data, product];
    return data;
  }, []);

  let getTDS = () => getFromData('allStrapiFamilies', (data, family) => {
    let docs = family.products;
    if (docs.length > 0) {
      return [
        ...data,
        {
          name: family.name,
          products: docs
        }
      ];
    }
    return data;
  });

  let getSDS = () => getFromData('allStrapiFamilies', (data, family) => {
    let docs = family.products;
    if (docs.length > 0) {
      return [
        ...data,
        {
          name: family.name,
          products: docs
        }
      ];
    }
    return data;
  });

  let getVideos = () => getFromData('allStrapiVideoGalleries', (data, video) => {
    return [
      ...data, {
      header: video.header,
      description: video.description,
      video: video.source,
      thumb: video.thumbVideoGallery.childImageSharp.fluid.src
    }]
  });

  let getCatalogs = () => getFromData('allStrapiCatalogs', (data, catalog) => {
    return [
      ...data, {
      name: catalog.name,
      description: catalog.description,
      file: get(catalog, 'fileCatalog.publicURL'),
      thumb: get(catalog, 'thumbCatalog.childImageSharp.fluid.src')
    }]
  });

  let getData = () => {
    let flow = props.path.split('/')[2] || 'catalogos';
    switch(flow) {
      case 'catalogos':
        return {
          title: 'Catálogos',
          description: ``,
          flow,
          items: getCatalogs()
        };
      case 'videos':
        return {
          title: 'Videos',
          description: ``,
          flow,
          items: getVideos()
        };
      case 'tds':
        return {
          title: 'Hojas Técnicas por Producto',
          description: ``,
          flow,
          items: getTDS()
        };
      case 'sds':
        return {
          title: 'Hojas de Seguridad por Producto',
          description: ``,
          flow,
          items: getSDS()
        };
    };
  };

  let contextValue = {
    getTDS,
    getSDS,
    getVideos,
    getCatalogs,
    getData
  };

  return (
    <LibraryState.Provider {...props} value={contextValue}/>
  );
};

export let useLibraryState = () => {
  let context = React.useContext(LibraryState);
  return context;
}

export default LibrarysProvider;
